    <h3>More about this project</h3>
    <p style="text-align: left; letter-spacing: 1px; line-height: 1.6; font-size: 18px">Hello! This project was my first real attempt at a full-stack data visualization. I built it because I found that it's 
    hard to keep track of news over time. I chose to use a bar chart because it allowed me to show more detail for each individual month.
    However, in hindsight a line graph would've been able to show the general trend of news better. Nonetheless, I think I achieved the goal I set out for.</p>
    <br>
    <p style="text-align: left; letter-spacing: 1px; line-height: 1.6; font-size: 18px;">For those interested in the technical side of this project, I chose to use a MEVN stack. MongoDB, Express, Vue, and Node. I also
