<template lang="eng">
    <div>
        <div>
            <h1>{{month + yearString}}</h1>
        </div>
        <div>
            <Spinner v-if = "start"></Spinner>
            <div class=articlesContainer v-show='showArt' v-on:click="noArticles()">
                <div id="articlesBox" class="articles">
                    <h2>{{selectedKeyword}}</h2>
                    <hr class="line">
                    <div class="article" v-for='element in articles'>
                        <div class="details">
                            <div class="title">
                                <h3 style="text-align: left">{{element.headline}}</h3>
                                <h4 style="display: inline; text-align: right;">{{element.pub_month + " " + element.pub_day + ", " + element.pub_year}}</h4>
                            </div>
                            <p style="text-align: left">{{element.abstract}}</p>
                            <a style="text-align: left; color: black":href='element.web_url' target="_blank">{{element.web_url}}</a>
                        </div>
                        <hr class="line">
                    </div>   
                     
                </div>
            </div>
            <svg v-bind:style="style" height="675px" width="1200px" overflow="auto">
                <defs>
                    <filter id="f1" x="0" y="0">
                        <feGaussianBlur in="SourceGraphic" stdDeviation="15" />
                    </filter>
                </defs>
                <rect v-on:click="showArticles($event)" @mouseleave= "mouseLeave($event)" @mouseover="mouseOver($event)" v-for='element in dataRange' :id='element.id' :x='element.x' :y='element.y' :width='element.width' :height='element.height' :fill='element.fill' v-bind:style='styleBlur'/>
                <g v-for='element in dataRange' v-show='element.show' :id='element.id + "-"'>
                    <text v-if='element.x < 850' :x='element.x + element.width + 2' :y='element.y + 20' font-family="Times New Roman" font-weight="bold">{{element.keyword + ": " + element.occurrances}}</text>
                    <text v-else :x='element.x - 2' :y='element.y + 20' font-family="Times New Roman" font-weight="bold" text-anchor="end">{{element.keyword + ": " + element.occurrances}}</text>
                </g>
                <g id="axis"></g>
                <text v-if="!start" x=15 y=370 font-family='sans-serif' font-size="16" font-weight="bold" transform="rotate(-90, 15, 370)"># of Occurrences on the Front Page</text>
                <g>
                    <line v-if="!start" x1="65" y1="627" x2="1200" y2="627" style="stroke:black; stroke-width:1"/>
                    <g v-for='element in alphabet'>
                        <line :x1='element.x':x2='element.x' y1='627' y2='633' style="stroke: black; stroke-width:1" />
                        <text font-family="sans-serif" font-size="12" :x='element.x - 5' :y='627 + 20'>{{ element.character }}</text>
                    </g>
                </g>
                <text v-if="!start" x='570' y='672' font-family="sans-serif" font-size="16" font-weight="bold">Keyword</text>
            </svg>
            <br><br>
            <div class="slidecontainer">
                <input type="range" min="1" max="12" value="12" class="slider" v-bind:value="value" v-on:input="sliderValue($event)">
            </div>
        </div>
    </div>
</template>


<script>
import * as d3 from 'd3';
import gsap from 'gsap';
import Spinner from '@/components/Spinner.vue';

export default {
    name: "ExampleData",
    components : {
        Spinner
    },
    props: ["myData", "start"], 

    data() {
        return {
            style : {
                opacity : "100%",
            },
           
            styleBlur : {
                filter: ""
            },
            selectedKeyword : "",
            showArt : false,
            articles : [],
            dataRange : [],
            january : [],
            february : [],
            march : [],
            april : [],
            may : [],
            june : [],
            july : [],
            august : [],
            september : [],
            october : [],
            november : [],
            december : [],
            alphabet : [],
            maxDom : 0,
            value : 12,
            month : "",
            yearString : "",
        }
    },
    methods : {
        calculateData :  function() {    
            this.yearString = ", " + this.myData[0].pub_date.substring(0,4);

            this.january = [];
            this.february = [];
            this.march = [];
            this.april = [];
            this.may = [];
            this.june = [];
            this.july = [];
            this.august = [];
            this.september  = [];
            this.october = [];
            this.november = [];
            this.december = [];


            //Sort into separate arrays
            for (let p = 0; p < this.myData.length; p++) {
                if(parseInt(this.myData[p].pub_date.substring(5,7)) == 1) {
                  this.january.push(this.myData[p]);  
                }
                
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 2) {
                  this.february.push(this.myData[p]);  
                }
                
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 3) {
                  this.march.push(this.myData[p]);  
                }
                
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 4) {
                  this.april.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 5) {
                  this.may.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 6) {
                  this.june.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 7) {
                  this.july.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 8) {
                  this.august.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 9) {
                  this.september.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 10) {
                  this.october.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 11) {
                  this.november.push(this.myData[p]);  
                }
                else if(parseInt(this.myData[p].pub_date.substring(5,7)) == 12) {
                  this.december.push(this.myData[p]);  
                }
                
            }
            
            let year = [];
            year.push(this.january);
            year.push(this.february);
            year.push(this.march);
            year.push(this.april);
            year.push(this.may);
            year.push(this.june);
            year.push(this.july);
            year.push(this.august);
            year.push(this.september);
            year.push(this.october);
            year.push(this.november);
            year.push(this.december);

            let m = 0;

            //Keyword Array
            for (let l = 0; l < year.length; l++) {
            let keywordArray = [];
            for (let i = 0; i < year[l].length; i++) {
                
                for (let j = 0; j < year[l][i].keywords.length; j++) {
                    let temp = year[l][i].keywords[j].value;
                    let found = false;
                    for (let k = 0; k < keywordArray.length; k++) {
                        if (keywordArray[k].keyword == temp) {
                            keywordArray[k].occurrances++;
                            found = true;
                            break;
                        }
                    }
                    if (found == false) {
                        let str = "id" + m.toString();
                        let tempObj = {
                            id : str,
                            keyword : temp,
                            occurrances : 1,
                            x: 0,
                            y: 0,
                            width: 0,
                            height: 0,
                            fill : 0,
                            show : false,
                            articles : []
                        } 
                        keywordArray.push(tempObj);
                        m++;
                    }
                }
            }
            year[l] = keywordArray;

            }
            
            //Only keep occurrances greater than 3
            for (let i = 0; i < year.length; i++) {
                let temp = [];
                for (let j = 0; j < year[i].length; j++) {
                    if (year[i][j].occurrances > 3) {
                        temp.push(year[i][j]);
                    }
                }
                year[i] = temp;
            }

            //Alphabetical order keywords
            for (let i = 0; i < year.length; i++) {
                
                year[i].sort(function(a, b) {
                    var nameA = a.keyword.toUpperCase(); // ignore upper and lowercase
                    var nameB = b.keyword.toUpperCase(); // ignore upper and lowercase
                    if (nameA < nameB) {
                        return -1;
                    }

                    if (nameA > nameB) {
                        return 1;
                    }
                    // names must be equal
                    return 0;
                });

            }

            //Dimensions and attributes
            for (let i = 0; i < year.length; i++) {
                
                let maxDomain = 0;
                for (let k = 0; k < year[i].length; k++) {
                    if (year[i][k].occurrances > maxDomain) {
                        maxDomain = year[i][k].occurrances;
                    }
                }

                let linearScale = d3.scaleLinear()
                    .domain([1, maxDomain])
                    .range([5,625]);

                let colorScale = d3.scaleLinear()
                    .domain([1, maxDomain])
                    .range([125, 50])


                for (let j = 0; j < year[i].length; j++) {
                    year[i][j].x =  65 + j * Math.trunc((1135 / year[i].length));
                    year[i][j].y = 625 - linearScale(year[i][j].occurrances);
                    year[i][j].width = Math.trunc((1135 / year[i].length)) - 1;
                    year[i][j].height = linearScale(year[i][j].occurrances);
                    let c = colorScale(year[i][j].occurrances);
                    year[i][j].fill = "rgb(" + 175 + ", " + c + ", " + c + ")";
                
                }
                
            }

            //Sort articles
            for (let i = 0; i < year.length; i++) {
                
                let date = (i + 1).toString();
                if (date.length == 1) {
                    date = "0" + date;
                }

                for (let j = 0; j < year[i].length; j++) {
                    let keyword = year[i][j].keyword;

                    for (let k = 0; k < this.myData.length; k++) {
                        for (let l = 0; l < this.myData[k].keywords.length; l++) {
                            
                           
                            if (this.myData[k].keywords[l].value == keyword && date == this.myData[k].pub_date.substring(5,7)) {
                                
                                let month = "";

                                if (i == 0) {
                                    month = "Jan.";
                                }
                                else if (i == 1) {
                                    month = "Feb.";
                                }
                                else if (i == 2) {
                                    month = "March";
                                }
                                else if (i == 3) {
                                    month = "April";
                                }
                                else if (i == 4) {
                                    month = "May";
                                }
                                else if (i == 5) {
                                    month = "June";
                                }
                                else if (i == 6) {
                                    month = "July";
                                }
                                else if (i == 7) {
                                    month = "Aug.";
                                }
                                else if (i == 8) {
                                    month = "Sept.";
                                }
                                else if (i == 9) {
                                    month = "Oct.";
                                }
                                else if (i == 10) {
                                    month = "Nov."
                                }
                                else if (i == 11) {
                                    month = "Dec.";
                                }

                                let tempObj = {
                                    web_url : this.myData[k].web_url,
                                    abstract : this.myData[k].abstract,
                                    headline : this.myData[k].headline.main,
                                    pub_day : parseInt(this.myData[k].pub_date.substring(8,10)),
                                    pub_month : month,
                                    pub_year  : this.myData[k].pub_date.substring(0,4),
                                    keyword : this.myData[k].keywords[l].value
                                }

                                year[i][j].articles.push(tempObj);


                            }
                        }
                    }
                }
            }

            //Reassign data as keyword array
            for (let p = 0; p < 12; p++) {
                if (p == 0) {
                  this.january = year[p];  
                }
                else if (p == 1) {
                  this.february = year[p];  
                }
                else if (p == 2) {
                  this.march = year[p];  
                }
                else if (p == 3) {
                  this.april = year[p];  
                }
                else if (p == 4) {
                  this.may = year[p];  
                }
                else if (p == 5) {
                  this.june = year[p];  
                }
                else if (p == 6) {
                  this.july = year[p];  
                }
                else if (p == 7) {
                  this.august = year[p];  
                }
                else if (p == 8) {
                  this.september = year[p];  
                }
                else if (p == 9) {
                  this.october = year[p];  
                }
                else if (p == 10) {
                  this.november = year[p];  
                }
                else if (p == 11) {
                  this.december = year[p];  
                }

                
                
            }

            //Set initial dataRange
            if (this.value == 1) {
                this.dataRange = this.january;
                this.month = "January";
            }
            else if (this.value == 2) {
                this.dataRange = this.february;
                this.month = "February";
            }
            else if (this.value == 3) {
                this.dataRange = this.march;
                this.month = "March";
            }
            else if (this.value == 4) {
                this.dataRange = this.april;
                this.month = "April";
            }
            else if (this.value == 5) {
                this.dataRange = this.may;
                this.month = "May"
            }
            else if (this.value == 6) {
                this.dataRange = this.june;
                this.month = "June"
            }
            else if (this.value == 7) {
                this.dataRange = this.july;
                this.month = "July"
            }
            else if (this.value == 8) {
                this.dataRange = this.august;
                this.month = "August"
            }
            else if (this.value == 9) {
                this.dataRange = this.september;
                this.month = "September"
            }
            else if (this.value == 10) {
                this.dataRange = this.october;
                this.month = "October"
            }
            else if (this.value == 11) {
                this.dataRange = this.november;
                this.month = "November"
            }
            else if (this.value == 12) {
                this.dataRange = this.december;
                this.month = "December"
            }

            this.alphabet = [];
            let previousChar = "";
            //set initial xAxis
            for (let i = 0; i < this.dataRange.length; i++) {
                let tempChar = this.dataRange[i].keyword.substring(0,1).toUpperCase();
                let tempObj = {
                    character : tempChar,
                    x : this.dataRange[i].x
                }
                if (tempChar != previousChar) {
                    this.alphabet.push(tempObj);
                    previousChar = tempChar;
                }
            }

            //set initial yAxis
            this.maxDom = 0;
            for (let x = 0; x < this.dataRange.length; x++) {
                if (this.dataRange[x].occurrances > this.maxDom) {
                    this.maxDom = this.dataRange[x].occurrances;
                }
            }

            this.renderAxis();

            //animate
            gsap.from(this.dataRange, {
                y : 650,
                duration : 1,
                stagger: 0.025

            }); 

       },
        
       //assign dataRange based on month 
        sliderValue : function(event) {
            this.value = event.target.value;
            this.dataRange = [];

             if (this.value == 1) {
                this.dataRange = this.january;
                this.month = "January";
            }
            else if (this.value == 2) {
                this.dataRange = this.february;
                this.month = "February";
            }
            else if (this.value == 3) {
                this.dataRange = this.march;
                this.month = "March";
            }
            else if (this.value == 4) {
                this.dataRange = this.april;
                this.month = "April";
            }
            else if (this.value == 5) {
                this.dataRange = this.may;
                this.month = "May";
            }
            else if (this.value == 6) {
                this.dataRange = this.june;
                this.month = "June";
            }
            else if (this.value == 7) {
                this.dataRange = this.july;
                this.month = "July";
            }
            else if (this.value == 8) {
                this.dataRange = this.august;
                this.month = "August";
            }
            else if (this.value == 9) {
                this.dataRange = this.september;
                this.month = "September";
            }
            else if (this.value == 10) {
                this.dataRange = this.october;
                this.month = "October";
            }
            else if (this.value == 11) {
                this.dataRange = this.november;
                this.month = "November";
            }
            else if (this.value == 12) {
                this.dataRange = this.december;
                this.month = "December";
            }

            this.maxDom = 0;
            for (let x = 0; x < this.dataRange.length; x++) {
                if (this.dataRange[x].occurrances > this.maxDom) {
                    this.maxDom = this.dataRange[x].occurrances;
                }
            }

            this.alphabet = [];
            let previousChar = "";
            //xAxis
            for (let i = 0; i < this.dataRange.length; i++) {
                let tempChar = this.dataRange[i].keyword.substring(0,1).toUpperCase();
                let tempObj = {
                    character : tempChar,
                    x : this.dataRange[i].x
                }
                if (tempChar != previousChar) {
                    this.alphabet.push(tempObj);
                    previousChar = tempChar;
                }
            }

            //animate
            gsap.from(this.dataRange, {
                y : 650,
                duration : 1,
                stagger: 0.025
            });
            
            this.renderAxis();
           
        }, 
        mouseOver : function(event) {
           event.target.style = "opacity: 50%";
           for (let i = 0; i < this.dataRange.length; i++) {
               if (event.target.id == this.dataRange[i].id) {
                   this.dataRange[i].show = true;
                   let str = "";
                   //let yStart = this.dataRange[i].y + 10;
                   str = "#" + this.dataRange[i].id + "-";
                   gsap.from(str, {
                       y: 15,
                       duration : 0.5,
                       opacity : 0
                   });
                   break;
               }
           }
           
           
       },
        mouseLeave : function(event) {
            event.target.style = "opacity: 100%";
           for (let i = 0; i < this.dataRange.length; i++) {
               if (event.target.id == this.dataRange[i].id) {
                   this.dataRange[i].show = false;
                   break;
               }
           }  
       },
        renderAxis : function() {
            let axisScale = d3.scaleLinear()
                .domain([this.maxDom, 1])
                .range([5, 625]);
            
            
            let yAxis = d3.axisLeft()
                .scale(axisScale)
                .ticks(20);
                
           d3.select("#axis")
            .attr("transform", "translate(60,0)")
            .style("font", "14px times")
            .style("stroke-width", "1px")
            .call(yAxis);
  
        }, 
        showArticles(event) {
            this.articles = [];
            for (let i = 0; i < this.dataRange.length; i++) {
               if (event.target.id == this.dataRange[i].id) {
                   this.articles = this.dataRange[i].articles;
                   this.selectedKeyword = this.dataRange[i].keyword;
               }
            }

            //alphabetical order
            this.articles.sort(function(a,b) {
                return a.pub_day - b.pub_day;
            });

            this.showArt = true;
            this.style.opacity = "50%";
            this.styleBlur.filter = "url(#f1)";

            //animate
            gsap.from("#articlesBox", {
                y : 50,
                duration : 0.25,
                opacity : 0

            }); 
        },
        noArticles() {
            this.showArt = false;
            this.style.opacity = "100%";
            this.styleBlur.filter = "";
        }
        
    
    },

    watch : {
        myData : function() {
            this.calculateData();
            this.$emit('apiCall');
            this.style.opacity = "100%";
        },

        start : function() {
            if (this.start == true) {
                this.style.opacity = "25%";
            }
            
        }
    }
}

</script>

<style>
    .articlesContainer {
        width : 1200px;
        height : 675px;
        position : absolute;
        z-index : 5;
       
    }

    .articles {
        background-color : white;
        border-style: solid;
        border-color : dimgrey;
        position : absolute;
        width : 70%;
        height : 80%;
        top : 5%;
        left : 18%;
        margin : auto;
        border-radius : 10px;
        z-index : 10;
        overflow: scroll;
    }

    .article {
        background-color : white;
        width : 96%;
        left: 2%;
        position : relative;

        
    }

    .title {
        display: flex;
        justify-content: space-between;
    }

    .line {
        width: 95%;
    }

    .details {
        margin-left: 20px;
        margin-right: 20px;
    }


    .slider {
        -webkit-appearance: none;
        width: 50%;
        height: 10px;
        border-radius: 5px;  
        background: #d3d3d3;
        outline: none;
        opacity: 0.7;
        -webkit-transition: .2s;
        transition: opacity .2s;
    }

    .slider::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 100px;
        height: 25px;
        border-radius: 5%;
        background: slategray;
        cursor: pointer;
    }

    .slider::-moz-range-thumb {
        width: 50px;
        height: 25px;
        border-radius: 5%;
        background: slategray;
        cursor: pointer;
    }
    
</style>